@import 'vars';

.root {
    background-color: #fff;
    min-height: 100%;

    .topPanel {
        padding: 20px 16px 16px 16px;
        background-color: $background-light;

        &.slim {
            padding: 12px 16px;

            border-bottom: 1px solid $background-light;
        }
    }

    .content {
        min-height: 100%;
    }

    &.padded .content {
        padding: 16px;
    }

    &.light .topPanel {
        background-color: #fff;
    }

    &.inverse {
        background-color: $background-light;
    }
}
