@import 'vars';
@import 'mixins/crop';

.publishedLabel {
    display: block;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    padding: 0 0 0 16px;
    position: relative;
}

.publishedDot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    left: 0;
    top: calc(50% - 5px);

    &.green {
        background-color: #3fb990;
    }
}
