.root {
    display: flex;
    align-items: center;
    width: 100%;
    height: 44px;
    margin: 12px 0 8px 0;
    padding: 0 16px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.08);
    position: relative;
    overflow: hidden;
    font-size: 14px;
    line-height: 24px;
    transition: box-shadow 0.6s ease, background-color 0.6s ease;
}

.personalLink {
    display: inline-block;
    vertical-align: top;
    max-width: 100%;
    font-weight: normal;
    color: #333;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-right: auto;
    white-space: nowrap;
    transition: color 0.3s ease;
}

.publishButton {
    display: flex;
    align-items: center;
    height: 100%;
    flex-shrink: 0;
    cursor: pointer;
    font-weight: 500;
    color: #007aff;
}

.copyButton {
    width: 44px;
    height: 100%;
    position: relative;
    flex-shrink: 0;
    cursor: pointer;
    margin: 0 -16px 0 0;

    &.copied {
        color: #07adff;

        > svg:nth-child(1) {
            opacity: 0;
            transform: translate(-50%, -50%) scale(0);
        }

        > svg:nth-child(2) {
            opacity: 1;
            transform: translate(-50%, -50%) scale(1);
        }
    }

    > svg:nth-child(2) {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0);
    }

    > svg,
    > img {
        display: block;
        width: 22px;
        height: 22px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform-origin: 50% 50%;
        transform: translate(-50%, -50%);
        transition: transform 0.3s ease, opacity 0.3s ease;
    }

    > svg {
        width: auto;
        height: auto;
    }
}

.notPublished {
    background-color: #EDEDF0;
    box-shadow: none;
    justify-content: center;

    .personalLink {
        margin-right: 0;
    }
}
