@import 'vars';

$canvas-border-radius: 0;
$canvas-top:  calc(54px + var(--safe-area-inset-top, 0));
$canvas-bottom: 86px;

.root {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: $background-light;
    overflow: hidden;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.5;
    color: #333;
}

.bgColor {
    position: absolute;
    top: $canvas-top;
    bottom: $canvas-bottom;
    left: 50%;
    transform: translateX(-50%);
    border-radius: $canvas-border-radius;

    //width: 100%;
    width: calc(100% - 80px);
    transition: width $scale-transition, bottom $scale-transition, top $scale-transition, box-shadow $scale-transition;
    will-change: width, bottom, top, box-shadow;

    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.04), 0 0 2px rgba(0, 0, 0, 0.06), 0 0 1px rgba(0, 0, 0, 0.04);

    &.viewMode {
        width: 100%;
        bottom: 0;
        top: 0;
        box-shadow: none;
        //border-radius: 20px;
    }

    @media all and (max-width: $mobile-breakpoint) {
        border-radius: 0;
    }
}

.bgImage {
    > img {
        overflow: hidden;
    }
}

.layout {
    width: 100%;
    height: 100%;
    z-index: 1;
}

.contentWrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.content {
    width: 100%;
    position: absolute;
    top: $canvas-top;
    left: 0;
    bottom: $canvas-bottom;
    overflow: auto;
    -webkit-overflow-scrolling: touch;

    transition: top $scale-transition, bottom $scale-transition;
    will-change: top, bottom;

    .contentInner {
        width: 600px;
        max-width: 100%;
        min-height: 100%;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        border-radius: $canvas-border-radius;
        margin: 0 auto;
    }

    &.viewMode {
        top: var(--safe-area-inset-top, 0);
        bottom: 0;
        padding-top: 0;
    }
}

.content::-webkit-scrollbar {
    width: 0;
}

.content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

.content::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0);
}

.content:hover::-webkit-scrollbar-thumb {
    background: rgba(200, 200, 200, 0.5);
}

.emptyPage {
    padding: 16px 56px !important;
}

.actionsContainer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
}

.actions {
    width: 600px;
    max-width: 100%;
    margin: 0 auto;
    position: relative;

    > * {
        pointer-events: auto;
    }
}
