.root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 58px;
    background-color: #2979ff;
    padding: 0 12px 0 41px;
}

.close {
    width: 41px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.content {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    overflow: hidden;

    > p {
        margin: 0;
        padding: 0 12px 0 0;
        font-size: 14px;
        line-height: 18px;
        color: #fff;
        flex-grow: 1;

        > span {
            font-weight: 600;
        }
    }
}

.button {
    display: block;
    border-radius: 100px;
    background-color: #fff;
    padding: 6px 8px;
    font-size: 14px;
    line-height: 16px;
    color: #2979ff;
    flex-grow: 0;
    flex-shrink: 0;
}
