@import 'vars';
@import 'mixins/crop';

.root {
    width: 100%;
    height: 54px;
    background-color: $background-light;
    position: absolute;
    top: var(--safe-area-inset-top, 0);
    left: 0;
    z-index: 10;
    //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    //border-bottom: 1px solid #eee;
    transition: background-color $default-transition, top $default-transition, box-shadow $default-transition;
    overflow: hidden;

    &.viewMode {
        top: -54px;
        box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    }

    .container {
        width: 100%;
        height: 200%;
        transition: transform 0.3s ease, height 0.3s ease;
    }

    &.sortMode {
        background-color: #fff;

        .container {
            //height: 0;
            transform: translateY(-50%);
        }
    }
}

.row {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.backButton {
    width: 54px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.button {
    width: 54px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.3s ease, opacity 0.3s ease;
    flex-shrink: 0;
}

.title {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex: 1 1 0;
    position: relative;
    overflow: hidden;
}

.edit {
    position: relative;
    pointer-events: none;
    flex: 0 0 32px;
}

.title .input {
    padding: 0 !important;
    border: none;
    flex: 1;
    display: flex;
    align-items: center;
}

.crop {
    @include crop();
}

.publishButton {
    user-select: none;
    margin-right: 10px;
}
