@import 'vars';

.root {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 999;

    &Legacy {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.5);
}

.sheet {
    position: absolute;
    bottom: 16px;
    left: 16px;
    width: calc(100% - 32px);
    max-height: calc(100% - 32px);
    display: flex;
    flex-direction: column;

    // TODO: redo, don't use nested selectors
    &.fullWidth {
        width: 100%;
        left: 0;
        bottom: 0;
        max-height: calc(100% - 16px - var(--safe-area-inset-top, 0));

        .sheetPane {
            border-radius: 16px 16px 0 0;
        }
    }

    // TODO: redo, don't use nested selectors
    &.fullHeight {
        height: 100% !important;
        max-height: 100%;

        .sheetPane {
            border-radius: 0;
        }

        .header {
            padding-top: var(--safe-area-inset-top, 0);
        }
    }

    &Legacy {
        position: relative;
        max-width: 600px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        bottom: auto;
        left: auto;
    }
}

.sheetPane {
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;
    padding-bottom: var(--safe-area-inset-bottom, 0);

    &.inverse {
        background-color: $background-light;
    }
}

.sheetyPane {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: hidden;
}

.header {
    flex-shrink: 0;
}

.padded {
    padding: 16px;
}

.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
}

.content::-webkit-scrollbar {
    width: 6px;
}

.content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}

.content::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 3px;
    transition: background 0.3s ease;
}
